				"html": "<p>When a <code>return</code> statement is reached, the execution of the current function stops and control returns to the calling location.</p>\n<h2>Example</h2>\n<pre><code>function myFun() {\n  console.log(\"Hello\");\n  return \"World\";\n  console.log(\"byebye\")\n}\nmyFun();\n</code></pre>\n<p>The above outputs \"Hello\" to the console, returns \"World\", but <code>\"byebye\"</code> is never output, because the function exits at the <code>return</code> statement.</p>",
